<template>
  <div class="home">
    <el-carousel :interval="4000" type="card" height="200px">
        <el-carousel-item v-for="(item,index) in imgArr" :key="index">
        <img :src="item" height="200" width="100%" alt="" srcset=""/>
        </el-carousel-item>
    </el-carousel>
    <div>
        <el-table
            :data="tableData3"
            style="width: 99%"
            height="250">
            <el-table-column
            fixed
            prop="date"
            label="日期"
            width="150">
            </el-table-column>
            <el-table-column
            prop="name"
            label="姓名"
            width="120">
            </el-table-column>
            <el-table-column
            prop="province"
            label="省份"
            width="120">
            </el-table-column>
            <el-table-column
            prop="city"
            label="市区"
            width="120">
            </el-table-column>
            <el-table-column
            prop="address"
            label="地址"
            width="300">
            </el-table-column>
            <el-table-column
            prop=""
            label=""
            >
            </el-table-column>
        </el-table>
        <!-- <img :src="mysrc" alt=""> -->
    </div>
    <el-button @click="postExamples()">增加数据</el-button>
    <el-button @click="getExamples()">获取数据</el-button>
    <input type="file" id="test">
    <el-button @click="upload()">上传</el-button>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'
import {index,postExample,getExample,getUsers,uploadFile} from '../api/api.js';
export default {
  name: 'home',
  data(){
    return{
        imgArr:['/img/test.jpg','/img/test.jpg','/img/test.jpg','/img/test.jpg','/img/test.jpg'],
        tableData3:[],
        mysrc:require('../assets/logo.png'),
        }  
  },
  components: {
    HelloWorld
  },
  created(){
        this.getList()
  },
  mounted(){
      
  },
  methods:{
      getList(){
         getUsers()
         .then((res)=>{
             console.log(res);
             this.tableData3=res
         })
         .catch(err=>{
             console.log(err);
         })
      },
      postExamples(){
        postExample({msg:"hello,新增数据"})
        .then((res)=>{
            console.log(res)
        })
          .catch((err) => {
            console.log(err);
        })
      },
      getExamples(){
          getExample()
          .then((res)=>{
              console.log(res)
          })
          .catch((err)=>{
              console.log(err);
          })
      },
      upload(){
          var formData=new FormData();
          var file=document.getElementById('test').files[0];
          formData.append("userfile",file);
          uploadFile(formData)
          .then(res=>{
              console.log(res);
          })
      }
  }
}
</script>
<style scoped>
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>
